import React from 'react';
import { activateWebXR } from '@r2u/react-ar-components'
function App() {
  const model:any = {
    resizable: true,
    name: 'Chair',
    description: 'Comfy chair',
    glb:
      'https://wsggbucket.s3.ap-northeast-2.amazonaws.com/Thonker.glb',
    url: 'https://google.com',
    placement: 'floor',
  }

  return (
    <div>
      <h1>WebXR</h1>
      <button type="button" onClick={() => activateWebXR({ model })}>
        Floor
      </button>
    </div>
  )
}

export default App;
